import React, {useState, useEffect} from 'react'
import './Navbar.css'
import logo from './logo.svg'
import {Link, NavLink} from 'react-router-dom'

export default function Navbar() {

    const [toggleMenu, setToggleMenu] = useState(false);
    const [largeur, setLargeur] = useState(window.innerWidth);

    const toggleNav = () => {
        setToggleMenu(!toggleMenu)
        console.log(toggleMenu)
    }

    useEffect(() => {

        const changeWidth = () => {
            setLargeur(window.innerWidth);
        }

        window.addEventListener('resize',changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }

    }, [])

    return (
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark">
            <div className="container-fluid">
                <NavLink exact activeClassName="active" to="/">
                    <li className="navbar-brand"> <img src={logo} className="website-logo" alt="logo" /> </li>
                </NavLink>
                <button className="navbar-toggler" aria-label="Toggle navigation" onClick={toggleNav} >
                    <span className="navbar-toggler-icon"></span>
                </button>
                {(toggleMenu || largeur > 990) && (
                    <div className="navbar-collapse justify-content-end" >
                        <div className="navbar-nav">
                            <NavLink exact activeClassName="active" to="/">
                                <li className="nav-link">Accueil</li>
                            </NavLink>
                            <NavLink exact activeClassName="active" to="/a-propos">
                            <li className="nav-link">A Propos</li>
                            </NavLink>
                            <NavLink exact activeClassName="active" to="/web">
                            <li className="nav-link">Web</li>
                            </NavLink>
                            <NavLink exact activeClassName="active" to="/print">
                            <li className="nav-link">Print</li>
                            </NavLink>
                            <NavLink exact activeClassName="active" to="/blog">
                            <li className="nav-link">Blog</li>
                            </NavLink>
                            <NavLink exact activeClassName="active" to="/contact">
                            <li className="nav-link">Contact</li>
                            </NavLink>
                        </div>
                    </div>
                )}
            </div>
        </nav>
    )
}
